import React from 'react'
import './index.less'
import {Button, Icon, message, notification} from 'antd';
import axios from "axios";
import {baseUrl} from "../../config";
import {CopyToClipboard} from "react-copy-to-clipboard";
import {Link} from "react-router-dom";
import wxQR from './wxQR.jpg';

const staticUrl = baseUrl;


export default class Withdraw extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            idForCopy:0,
        };
        console.log(staticUrl);
    }
    componentDidMount() {
        axios.get(
            baseUrl + '/api/ti_xian',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                this.setState({
                    idForCopy: Response.data.id,
                });
            });
    };
    infoCopied = () => {
        message.success('已复制到剪贴板，快去安排吧～');
    };
    render() {
        return (
            <div>
                <h1>提现</h1>
                <p>提现须知：
                    <br/>
                    1.每次提现固定提1000硬币(10元)。
                    <br/>
                    2.提现需3%手续费，即1000硬币可得 9.7元。
                    <br/>
                    3.请添加下方工作人员微信，告知提现id。
                </p>
                <div>
                    <span>
                        提现ID:{this.state.idForCopy}
                    </span>
                    < CopyToClipboard text={this.state.idForCopy}
                                      onCopy={this.onCopy}>
                        <Button className='button-copy' onClick={this.infoCopied}>
                            一键复制
                        </Button>
                    </CopyToClipboard>
                </div>
                <img src={wxQR} alt="" width='100%'/>
            </div>
        )
    }
}